import React, { Suspense} from 'react';
import { Routes, Route } from 'react-router-dom';
import './scss/style.scss';
import Header from './components/Header/Header';
import Home from './pages/Home';
import Footer from './components/Footer/Footer';
import Fallback from './common/Fallback';
import NotFound from './components/NotFound';
const CatalogPage = React.lazy(() => import('./pages/CatalogPage'));
const ProductPage = React.lazy(() => import('./pages/ProductPage'));
const SoobshitOshibku = React.lazy(() => import('./pages/SoobshitOshibku'));
const Sotrudnicestvo = React.lazy(() => import('./pages/Sotrudnicestvo'));
const Principi = React.lazy(() => import('./pages/Principi'));
const Kontakty = React.lazy(() => import('./pages/Kontakty'));
const Konsultaciya = React.lazy(() => import('./pages/Konsultaciya'));

const App: React.FC = () => {
  return (
    <>
      <Header />
      <main className='page'>
        <Suspense fallback={<Fallback />}>
        <Routes>
          <Route path="nbhunter/" element={<Home />} />
          <Route path="nbhunter/product/:id" element={<ProductPage />} />
          <Route path="nbhunter/catalog" element={<CatalogPage />} />
          <Route path="nbhunter/sotrudnichestvo" element={<Sotrudnicestvo/>} />
          <Route path="nbhunter/soobshit-ob-oshibke" element={<SoobshitOshibku/>} />
          <Route path="nbhunter/principi" element={<Principi/>} />
          <Route path="nbhunter/kontakty" element={<Kontakty/>} />
          <Route path="nbhunter/konsultaciya" element={<Konsultaciya/>} />
          <Route path="nbhunter/*" element={<NotFound />} />
          <Route path="nbhunter/notfound" element={<NotFound />} />
        </Routes>
        </Suspense>
      </main>
      <Footer />
    </>
  )
}

export default App;
