import { useEffect } from 'react';
import { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { fetchRecProducts } from '../redux/rec-products/actions';
import { useAppDispatch, useAppSelector } from '../redux/store';
import ProductBlock from './ProductBlock/ProductBlock';
import Skeleton from './ProductBlock/Skeleton';

const MainSwiper = () => {
  const { items } = useAppSelector(state => state.recProducts)
  const dispatch = useAppDispatch()
  useEffect(() => {
    dispatch(fetchRecProducts())
    // eslint-disable-next-line react-hooks/exhaustive-deps 
  }, [])
  return (
    <Swiper
      modules={[Navigation]}
      spaceBetween={18}
      slidesPerView={1}
      navigation={{
        nextEl: '.rec-catalog__nav--next',
        prevEl: '.rec-catalog__nav--prev',
      }}
      breakpoints={{
        520: {
          slidesPerView: 2,
        },
        768: {
          slidesPerView: 3,
        },
        992: {
          slidesPerView: 4,
        },
      }}
      speed={800}
      className="rec-catalog__swiper swiper">
      {items.length > 0 ? items.map(obj => {
        return <SwiperSlide key={obj.id} className="rec-catalog__slide swiper-slide">
          <ProductBlock {...obj} />
        </SwiperSlide>
      }) : [...new Array(8)].map((item,i) => {
        return <SwiperSlide key={i} className="rec-catalog__slide swiper-slide">
          <Skeleton  />
        </SwiperSlide>
      })}
      <div className="rec-catalog__navs">
        <div className="rec-catalog__nav rec-catalog__nav--prev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><g data-name="21.Arrow Left"><path d="M12 24a12 12 0 1 1 12-12 12.013 12.013 0 0 1-12 12zm0-22a10 10 0 1 0 10 10A10.011 10.011 0 0 0 12 2z"/><path d="M10.293 16.707 5.586 12l4.707-4.707 1.414 1.414L8.414 12l3.293 3.293-1.414 1.414z"/><path d="M7 11h11v2H7z"/></g></svg></div>
        <div className="rec-catalog__nav rec-catalog__nav--next"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><g data-name="20.Arrow Right"><path d="M12 24a12 12 0 1 1 12-12 12.013 12.013 0 0 1-12 12zm0-22a10 10 0 1 0 10 10A10.011 10.011 0 0 0 12 2z"/><path d="m13.707 16.707-1.414-1.414L15.586 12l-3.293-3.293 1.414-1.414L18.414 12l-4.707 4.707z"/><path d="M6 11h11v2H6z"/></g></svg></div>
      </div>
    </Swiper>
  )
}
export default MainSwiper
