import { HashLink } from "react-router-hash-link"
import logo from "../../assets/images/logo-footer.png"
import { useAppDispatch } from "../../redux/store"
import { resetTagsAndSetNewTag } from "../../redux/tag-products/actions"
const Footer:React.FC = () => {
  const dispatch = useAppDispatch()
  return (
    <footer className="footer" id="about">
    <div className="footer__top">
      <div className="container footer__container">
      <div className="footer__column text-footer">
            <p>NbHunter.ru - онлайн консультант по ноутбукам. На нашем сайте вы можете найти подходящий Вам ноутбук по выбранным Вами тегам.</p>       
            <p>Сервис рассчитан для людей, которые не сильно разбираются в железе или не разбираются в нем вообще. Мы не продаем ноутбуки, а только указываем подробные характеристики, цену, минусы и плюсы аппарата (на основе отзывов).</p>
            <p>Все ссылки на ноутбуки введут только к проверенным продавцам: М.Видео, Ситилинк, ДНС, KNS, АлиЭкспресс, Computer Universe (Германия)</p>
            <p>Под меню сайта Вы можете обратить внимание на лучший ноутбук по соотношению цена и качество на текущий момент.</p>
            <span>ВНИМАНИЕ! Сайт NbHunter не несет никакой ответственности за  изменения ценника на сайте продавца. Вся информация на сайте NbHunter несет только ознакомительный характер. При переходе на сайт продавца стоит еще раз обратить внимание на актуальность старого ценника на ноутбук.</span>
          </div>
          <div className="footer__column info-footer">
            <div className="info-footer__columns">
              <div className="info-footer__column col-info">
                <div className="col-info__header">NbHunter</div>
                <ul className="col-info__list">
                  <li>
                    <HashLink to={"nbhunter/sotrudnichestvo"} smooth className="col-info__link">Сотрудничество</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/soobshit-ob-oshibke"} smooth className="col-info__link">Сообщить об ошибке</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/principi"} smooth className="col-info__link">Принципы проекта</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/kontakty"} smooth className="col-info__link">Контакты</HashLink>
                  </li>
                </ul>
              </div>
              <div className="info-footer__column col-info">
                <div className="col-info__header">Ноутбуки</div>
                <ul className="col-info__list">
                  <li>
                    <HashLink to={"nbhunter/#tags"} onClick={()=>dispatch(resetTagsAndSetNewTag())} smooth className="col-info__link">Подобрать ноутбук</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/catalog"} smooth className="col-info__link">Полный каталог</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/#tags"} onClick={()=>dispatch(resetTagsAndSetNewTag("graphic-design"))} smooth className="col-info__link">Графический дизайн</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/#tags"} onClick={()=>dispatch(resetTagsAndSetNewTag("office-work"))} smooth className="col-info__link">Офисные</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/#tags"} onClick={()=>dispatch(resetTagsAndSetNewTag("gaming"))} smooth className="col-info__link">Игровые</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/#tags"} onClick={()=>dispatch(resetTagsAndSetNewTag("3ds-max"))} smooth className="col-info__link">3D графика</HashLink>
                  </li>
                  <li>
                    <HashLink to={"nbhunter/#tags"} onClick={()=>dispatch(resetTagsAndSetNewTag("web-design"))} smooth className="col-info__link">Веб-дизайн</HashLink>
                  </li>
                </ul>
              </div> 
              <div className="info-footer__column col-info">
                <div className="col-info__header">Услуги</div>
                <ul className="col-info__list">
                  <li>
                    <HashLink to={"nbhunter/konsultaciya"} smooth className="col-info__link">Консультация</HashLink>
                  </li>
                </ul>
              </div>
            </div>
            <div className="info-footer__attention">
              <div className="attention-info">
                <div className="attention-info__text">
                  На сайте указаны не все модели рынка. Мы добавляем только самые интересные на наш взгляд модели. К нам на сайт также не попадают модели, которые имеют очень много плохих отзывов,  модели с очень слабым железом, а также модели от неизвестных (или малоизвестных) производителей.
                </div>
              </div>
            </div>
          </div>
      </div>
    </div>
    <div className="footer__footer footer-footer">
      <div className="footer-footer__container container">
        <div className="footer-footer__inner">
          <div className="footer-footer__copyright">
            <a href="#">
              <img src={logo} alt="logo" />
            </a>
            <span>© 2022</span>
          </div>
          <div className="footer-footer__contacts">
            <a href="mailto:mail@nbhunter.ru">mail@nbhunter.ru</a><span>|</span>
            <a href="tel:89999999999">+7 999 999 99 99</a><span>|</span>г. Санкт-Петербург
          </div>
        </div>
      </div>
    </div>
  </footer>
  )
} 
export default Footer