import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { FetchStatusType } from '../../common/types'
import { fetchProducts } from './actions'
import { ProductsSliceState } from './types'

const initialState: ProductsSliceState = {
  allItems: [],
  items: [],
  tags: [],
  currPage: 1,
  itemsPerPage: 12,
  totalCount: 0,  
  sortProperty: "rating",
  orderProperty: "desc",
  fetchStatus: FetchStatusType.pending
}

export const productsSlice = createSlice({
  name: 'products',
  initialState,
  reducers: {
    setTags: (state, {payload}: PayloadAction<string>) => {
      const findTag = state.tags.find(item => item === payload)
      findTag ? state.tags = state.tags.filter(item => item !== payload) : state.tags.push(payload)
      state.currPage = 1
    },
    setPage: (state, {payload}: PayloadAction<number>) => {
      state.currPage = payload
    },
    setSort: (state, { payload }: PayloadAction<Record<string,string>>) => {
      state.currPage = 1
      state.sortProperty = payload.sort
      state.orderProperty = payload.order
    },
    setFilters: (state, {payload}: PayloadAction<Record<string, string>>) => {
      state.currPage = Number(payload.p)
      state.itemsPerPage = Number(payload.l)
      state.sortProperty = payload.sortBy
      state.orderProperty = payload.order
      let payloadTags = payload.tags ? payload.tags.toString().split(",") : []
      if (payloadTags.length) payloadTags.forEach(item=> state.tags.push(item))
    },
    resetTags: state => {
      state.tags = []
    }
  },
  extraReducers: (builder) => {
    builder.addCase(fetchProducts.pending, state => {  
      state.fetchStatus = FetchStatusType.pending 
    }); 
    builder.addCase(fetchProducts.fulfilled, (state, {payload}) => {   
      state.fetchStatus = FetchStatusType.fulfilled
      state.allItems = payload.items    
      state.items = [...payload.items.slice((state.currPage - 1 ) * state.itemsPerPage,state.currPage * state.itemsPerPage)]
      state.totalCount = payload.count
    });
    builder.addCase(fetchProducts.rejected, state => { 
      state.fetchStatus = FetchStatusType.rejected
      state.items = []
      state.totalCount = 0
    });
  }
})

export const {setTags, setPage,setSort, setFilters, resetTags} = productsSlice.actions

export default productsSlice.reducer