import { ProductBlockType } from "../common/types"

export default function inputTotalCount(items: ProductBlockType[] , item: string, value: string) {
  let findObj = items.filter(obj => {
    let key = item as keyof typeof obj
    let val = obj[key]
    if (val && Array.isArray(val) && val.includes(value)) {
      return obj
    }
    return null
  })
  return findObj ? findObj.length : 0
}