export const filtersSlice = createSlice({
  name: 'filters',
  initialState,
  reducers: {
    setPage: (state, { payload }: PayloadAction<number>) => {
      state.currPage = payload
    },
    setSearchValue: (state, { payload }: PayloadAction<string>) => {
      state.currPage = 1
      state.searchValue = payload
    },
    setPriceStart: (state, { payload }: PayloadAction<number>) => {
      state.currPage = 1
      state.priceStart = payload
    },
    setPriceEnd: (state, { payload }: PayloadAction<number>) => {
      state.currPage = 1
      state.priceEnd = payload
    },
    setSort: (state, { payload }: PayloadAction<Record<string, string>>) => {
      state.currPage = 1
      state.sortProperty = payload.sort
      state.orderProperty = payload.order
    },
    setFilters: (state, { payload }: PayloadAction<Record<string, string>>) => {
      state.currPage = Number(payload._page)
      state.itemsPerPage = Number(payload._limit)
      state.sortProperty = payload._sort
      state.orderProperty = payload._order     
      state.priceStart = Number(payload.price_gte)
      state.priceEnd = Number(payload.price_lte)
      if (payload.q) {
        state.searchValue = payload.q
      }
      for (let key in payload) {
        if (key !== "_page" && key !== "_limit" && key !== "_sort" && key !== "_order" && key !== "q"&& key !== "price_gte"&& key !== "price_lte") {
          let keyPorth = key.indexOf(".")
          state.filters = [...state.filters, {name: key.substring(0,keyPorth), values: payload[key].split("|") }]
          let findInput = state.inputs.find(item => item.translitname === key.substring(0,keyPorth))
          if (findInput) findInput.properties.find(el => {
            if (payload[key].split("|").includes(el.translitvalue)) {
              el.checked = !el.checked
            }
            return null
          })
        }
      }
    },
    checkInput: (state, { payload }: PayloadAction<string[]>) => {
      state.currPage = 1
      let findInput = state.inputs.find(item => item.translitname === payload[0])
      if (findInput) findInput.properties.find(el => {
        if (el.translitvalue === payload[1]) {
          el.checked = !el.checked
        }
        return null
      })
      let findItem = state.filters.find(item => item.name === payload[0])
      if (findItem) {
        if (findItem.values.includes(payload[1])) {
          if(findItem.values.length === 1) {
            state.filters = state.filters.filter(item=>item.name !== payload[0])
          } else {
            state.filters = state.filters.map(item=> {
              if (item.name === payload[0]) {
                return {...item, values: item.values.filter(el => el !== payload[1])}
              }
              return item
            })
          }
        } else {
          state.filters = state.filters.map(item=> {
            if (item.name === payload[0]) {
              return {...item, values: [...item.values,payload[1]]}
            }
            return item
          })
        }
      } else {
        state.filters = [...state.filters,{name: payload[0], values: [payload[1]]}]
      }
    },
    resetInputs: (state) => {
      state.currPage = 1
      state.filters = []
      state.priceStart = 22999
      state.priceEnd = 65999
      state.inputs.forEach(item => {
        item.properties.forEach(el => {
          el.checked = false
        })
      })
    },
    setData: (state) => {
      state.inputs = state.inputs.map(input => {
        return {
          ...input,
          properties: input.properties.map(el => {
            let _filters = state.filters.filter(item => item.name !== input.translitname)
            _data = data.filter(obj => {
              let ifEveryObjHasFilter = _filters.every(item => {
                let key = item.name as keyof typeof obj
                let val = obj[key]
                if (val && Array.isArray(val) && item.values.includes(val[1])) {
                  return item
                }
                return null
              })
              if (obj.price >= state.priceStart && obj.price <= state.priceEnd && ifEveryObjHasFilter) {
                return obj
              }
              return null
            })
            return {
              ...el,
              count: inputTotalCount(_data, input.translitname, el.translitvalue)
            }
          })
        }
      })

    }
  },
  extraReducers: (builder) => {
    builder.addCase(fetchFiltersProducts.pending, state => {
      state.fetchStatus = FetchStatusType.pending
    });
    builder.addCase(fetchFiltersProducts.fulfilled, (state, { payload }) => {
      state.fetchStatus = FetchStatusType.fulfilled
      state.items = payload.items
      state.totalCount = payload.count ? Number(payload.count) : 0
    });
    builder.addCase(fetchFiltersProducts.rejected, state => {
      state.fetchStatus = FetchStatusType.rejected
      state.items = []
    });
  }
})
export const { setPage, setSearchValue, setSort, setFilters, checkInput, resetInputs, setData, setPriceStart, setPriceEnd } = filtersSlice.actions

export default filtersSlice.reducer

