import clsx from "clsx";
import { useEffect, useState } from "react";
import { NavLink} from "react-router-dom";
import { HashLink } from 'react-router-hash-link';
import logo from "../../assets/images/logo.png"
const Header = () => {
  const [open, setOpen] = useState(false)
  useEffect(()=>{
    const items = document.querySelectorAll(".menu__link") as NodeListOf<HTMLElement>
    for (let i = 0; i < items.length; i++) {
      items[i].style.setProperty('--item-index', String(i))
    }
  },[])
  
  const burgerOnClick = () => {
    if (!open) {
      document.body.classList.add("no-scroll")
    } else {
      document.body.classList.remove("no-scroll")
    }
    setOpen(!open)
  }
  const closeBurger = () => {
    setOpen(false)
    document.body.classList.remove("no-scroll")
  }
  return (
    <header className="header">
      <div className="header__container container">
        <div className="header__inner">
          <div className="header__logo logo-header">
            <NavLink to={"nbhunter/"} className="logo-header__logo">
              <img src={logo} alt="Logo" />
            </NavLink>
            <div className="logo-header__text">Онлайн-консультант по ноутбукам</div>
          </div>
          <div className="header__menu menu">
            <div onClick={()=> burgerOnClick()} className={clsx("menu__icon icon-menu", {"active": open})}>
              <div className="icon-menu__item"></div>
            </div>
            <nav className={clsx("menu__body", {"active": open})}>
              <ul className="menu__list">
                <li className="menu__item">
                  <NavLink to={"nbhunter/"} onClick={()=>closeBurger()} preventScrollReset={true} className={isActive => isActive.isActive ? "menu__link active": "menu__link"}>Главная</NavLink>
                </li>
                <li className="menu__item">
                  <HashLink to={"nbhunter/#recommended"} onClick={()=>closeBurger()} smooth className="menu__link">Наша подборка</HashLink>
                </li>
                <li className="menu__item">
                  <HashLink to={"nbhunter/#tags"} onClick={()=>closeBurger()} smooth className="menu__link">По тегам</HashLink>
                </li>
                <li className="menu__item">
                  <NavLink to={"nbhunter/catalog"} onClick={()=>closeBurger()} preventScrollReset={true} className={isActive => isActive.isActive ? "menu__link active": "menu__link"}>Все ноутбуки</NavLink>
                </li>
                <li className="menu__item">
                  <HashLink to={"nbhunter/#about"} onClick={()=>closeBurger()} smooth className="menu__link">О проекте</HashLink>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </header>
  )
}
export default Header