import { createSlice } from '@reduxjs/toolkit'
import { FetchStatusType } from '../../common/types'
import { fetchProduct } from './actions'
import { ProductSliceState } from './types'

const initialState: ProductSliceState = {
  item: {
    "id": "",
    "name": "",
    "price": "",
    "imagesBig": [],
    "imagesSmall": [],
    "properties": {
      "keys": [],
      "all": [],
      "extra": []
    }
  },
  fetchStatus: FetchStatusType.pending
}

export const productSlice = createSlice({
  name: 'product',
  initialState,
  reducers: {
  },
  extraReducers: (builder) => {
    builder.addCase(fetchProduct.pending, state => {
      state.fetchStatus = FetchStatusType.pending
    });
    builder.addCase(fetchProduct.fulfilled, (state, { payload }) => {
      state.fetchStatus = FetchStatusType.fulfilled
      state.item = payload
    });
    builder.addCase(fetchProduct.rejected, state => {
      state.fetchStatus = FetchStatusType.rejected
      state.item = {
        "id": "",
        "name": "",
        "price": "",
        "imagesBig": [],
        "imagesSmall": [],
        "properties": {
          "keys": [],
          "all": [],
          "extra": []
        }
      }
    });
  }
})


export default productSlice.reducer