import axios  from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';
export const fetchRecProducts = createAsyncThunk(
  'recproducts/fetchRecProductsStatus',
  async () => {
    const response = await axios.get(`https://636d3e5791576e19e323e306.mockapi.io/rec`)
    return response.data
  }
) 