import { NavLink } from "react-router-dom"

const Intro: React.FC = () => {
  const handleClick = () => {
    window.open('https://www.mvideo.ru', '_blank');
  };
  return (
    <section className="page__intro intro-page">
      <div className="container intro-page__container">
      <div className="intro-page__btns">
        <NavLink to={"/nbhunter/product/39"} className="btn intro-page__btn">Все характеристики</NavLink>
        <button onClick={()=>handleClick()} className="btn intro-page__btn">MVideo.ru</button>
      </div>
      </div>
    </section>
  )
}
export default Intro