import { ProductBlockType } from './../../common/types';
import { createAsyncThunk } from "@reduxjs/toolkit"
import axios from "axios"
import { checkInput, resetInputs, setData } from "./slice"
import { FetchFiltersType } from "./types"

export const fetchFiltersProducts = createAsyncThunk(
  'filters/fetchFiltersStatus',
  async ({currPage, itemsPerPage, searchValue, sortProperty, orderProperty, filterItems, priceStart, priceEnd}: FetchFiltersType) => {  
    const response = await axios.get(`http://localhost:3001/laptops?${filterItems ? `${filterItems.join("&")}&`: ""}${searchValue ? `q=${searchValue}&`: ""}price_gte=${priceStart}&price_lte=${priceEnd}&_page=${currPage}&_limit=${itemsPerPage}&_sort=${sortProperty}&_order=${orderProperty}`)
    let data = {items: [...response.data],count: response.headers["x-total-count"]} 
    return data as {items: ProductBlockType[], count: string | undefined}
  }
) 
export const setFiltersAndInputs = createAsyncThunk(
  'filters/setFiltersAndInputs',
  async ( filterArr:string[], {dispatch}) => {  
    await dispatch(checkInput([...filterArr]))
    dispatch(setData())
  }
) 
export const resetFiltersAndInputs = createAsyncThunk(
  'filters/resetFiltersAndInputs',
  async ( params = undefined, {dispatch}) => { 
    await dispatch(resetInputs())
    dispatch(setData())
  }
) 