import React from "react"
import ContentLoader from "react-content-loader"

const Skeleton:React.FC = () => (
  <ContentLoader className="item-catalog"
    speed={2}
    width={270}
    height={433}
    viewBox="0 0 270 433"
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
  >
    <rect x="35" y="215" rx="0" ry="0" width="200" height="24" /> 
    <rect x="35" y="250" rx="0" ry="0" width="200" height="63" /> 
    <rect x="58" y="328" rx="0" ry="0" width="160" height="24" /> 
    <rect x="62" y="391" rx="0" ry="0" width="150" height="23" /> 
    <rect x="35" y="5" rx="0" ry="0" width="200" height="200" />
  </ContentLoader>
)

export default Skeleton
