import { createAsyncThunk } from "@reduxjs/toolkit"
import axios from "axios"
import { ProductBlockType } from "../../common/types"
import { FetchProductsType } from "./types"
import { resetTags, setTags } from "./slice"


export const fetchProducts = createAsyncThunk(
  'products/fetchProductsStatus',
  async ({ tags, currPage, sortProperty, orderProperty, itemsPerPage }: FetchProductsType) => {
    const response = await axios.get(`https://636d3e5791576e19e323e306.mockapi.io/tagproducts?sortBy=${sortProperty}&order=${orderProperty}`)
    let data: ProductBlockType[] = response.data.items
    let filteredByTags = tags.length ? data.filter(arr => arr.tags && tags.every(item => arr.tags?.includes(item))) : data
    return {
      items: filteredByTags,
      count: filteredByTags.length
    }
  }
)
export const resetTagsAndSetNewTag = createAsyncThunk(
  'filters/resetTagsAndSetNewTag',
  async ( item: string | undefined, {dispatch}) => { 
    await dispatch(resetTags())
    if (item) dispatch(setTags(item))
  }
) 


