import { ActionCreatorWithPayload } from "@reduxjs/toolkit"
import clsx from "clsx"
import { useState, useEffect, useRef } from "react"
import { useAppDispatch } from "../redux/store"
import smoothDrop from "../utils/smoothDrop"
type PopupClick = MouseEvent & {
  path: Node[]
}
type SortPropType = {
  sortProperty: string;
  orderProperty: string;
  setSort: ActionCreatorWithPayload<Record<string, string>, string>
}
const Sort: React.FC<SortPropType> = ({ sortProperty, orderProperty, setSort }) => {
  const [open, setOpen] = useState(false)
  const dispatch = useAppDispatch()
  const sortRef = useRef<HTMLDivElement>(null)
  const animSppd = 300
  const sortList: Record<string, string>[] = [
    { name: "По цене", sort: "price", order: "asc" },
    { name: "По цене", sort: "price", order: "desc" },
    { name: "По рейтингу", sort: "rating", order: "asc" },
    { name: "По рейтингу", sort: "rating", order: "desc" }
  ]
  let findProperty = sortList.find(item => item.sort === sortProperty && item.order === orderProperty)
  let sortName = findProperty ? findProperty.name : ""
  const selectOnClick = () => {
    setOpen(!open)
    if (sortRef.current) {
      const selectSelected = sortRef.current.querySelector(".select-custom__selected") as HTMLElement
      const selectOptns = sortRef.current.querySelector(".select-custom__options") as HTMLElement
      smoothDrop(selectSelected,selectOptns)
    }
  }
  const onClickSort = (item: Record<string, string>) => {
    selectOnClick()
    setTimeout(() => {
      dispatch(setSort(item)) 
    }, animSppd);     
  }

  useEffect(() => { 
    if (open) {
      const clickOutside = (e: MouseEvent) => {
        const _e = e as PopupClick
        if ( sortRef.current  && ! _e.path.includes(sortRef.current)) selectOnClick()
      }
      document.addEventListener("click", clickOutside)
      return () => document.removeEventListener("click", clickOutside)
    }
     // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open])
  return (
    <div ref={sortRef} className={clsx("select-custom")}>
      <div onClick={() => selectOnClick()} className="select-custom__selected">{sortName}</div>
      <div className="select-custom__options">
        {sortList.map((item, i) => <div key={i} onClick={() => onClickSort(item)} className={clsx("select-custom__option", {"select-custom__option--desc": item.order === "desc"},{"active": (findProperty?.sort === item.sort && findProperty?.order === item.order)})}>{item.name}</div>)}
      </div>
    </div>
  )
}
export default Sort