import { createSlice } from '@reduxjs/toolkit'
import { FetchStatusType } from '../../common/types'
import { fetchRecProducts } from './actions'
import { RecProductsSliceState } from './types'

const initialState: RecProductsSliceState = {
  items: [],
  totalCount: 0,
  fetchStatus: FetchStatusType.pending
}

export const recProductsSlice = createSlice({
  name: 'recproducts',
  initialState,
  reducers: {
  },
  extraReducers: (builder) => {
    builder.addCase(fetchRecProducts.fulfilled, (state, {payload}) => {   
      state.fetchStatus = FetchStatusType.fulfilled
      state.items = payload.items
      state.totalCount = payload.count
    });
    builder.addCase(fetchRecProducts.rejected, state => { 
      state.fetchStatus = FetchStatusType.rejected
      state.items = []
      state.totalCount = 0
    });
  }
})


export default recProductsSlice.reducer