import React, { useEffect, useRef, useState } from "react"
import { useNavigate, useSearchParams } from "react-router-dom"
import { fetchProducts } from "../../redux/tag-products/actions"
import { useAppDispatch, useAppSelector } from "../../redux/store"
import ProductBlock from "../ProductBlock/ProductBlock"
import Skeleton from "../ProductBlock/Skeleton"
import Tags from "./Tags"
import { setFilters, setPage, setSort } from "../../redux/tag-products/slice"
import Sort from "../Sort"
import Loading from "../../common/Loading"

const TagsPage = React.forwardRef(() => {
  const dispatch = useAppDispatch()
  let isMounted = useRef(false)
  const navigate = useNavigate()

  const searchParams = useSearchParams()[0].entries()
  const { fetchStatus, items, tags, sortProperty, orderProperty, currPage, itemsPerPage, totalCount } = useAppSelector(state => state.products)
  const [hasMore, setHasMore] = useState(false)
  const [hasPrev, setHasPrev] = useState(false)
  const [products, setProducts] = useState([...items])
  const isLocationSeacrh = useRef(false)
  const currPageRef = useRef(1)
  const isFirstItems = useRef(true)
  const [isLoading, setIsLoading] = useState(false)
  const firstLastPage = useRef({ firstPage: currPage, lastPage: currPage })
  const prevMoreLoading = useRef({ prev: false, more: false })
  function fetchAllProducts():void {
    const params = {
      tags,
      sortProperty,
      orderProperty,
      currPage,
      itemsPerPage
    }
    dispatch(fetchProducts({ ...params }))
    setIsLoading(true)
    if (isMounted.current) {
      navigate(`?${tags.length ? `tags=${tags}&` : ""}p=${firstLastPage.current.lastPage}&l=${itemsPerPage}&sortBy=${sortProperty}&order=${orderProperty}`)
      isLocationSeacrh.current = false
    }
  }
  const loadMoreOnClick = () => {
    dispatch(setPage(firstLastPage.current.lastPage + 1))
    firstLastPage.current.lastPage = firstLastPage.current.lastPage + 1
    isFirstItems.current = false
    prevMoreLoading.current.more = true
  }
  const loadPrevOnClick = () => {
    dispatch(setPage(firstLastPage.current.firstPage - 1))
    firstLastPage.current.firstPage = firstLastPage.current.firstPage - 1
    isFirstItems.current = false
    prevMoreLoading.current.prev = true
  }
  useEffect(() => {
    if (items.length === 0 && currPage !==1) {
      dispatch(setPage(1))
      firstLastPage.current = { firstPage: 1, lastPage: 1 }
    } else {
      if (currPageRef.current !== currPage && products.length > 0) {
        if (items[0].id > products[0].id) {
          setProducts([...products, ...items])
        } else {
          setProducts([...items, ...products])
        }
      } else {
        setProducts([...items])
      }
      prevMoreLoading.current.more = false
      prevMoreLoading.current.prev = false
      setIsLoading(false)
    }
    currPageRef.current = currPage
    // eslint-disable-next-line react-hooks/exhaustive-deps 
  }, [items])
  useEffect(() => {
    if (!isLocationSeacrh.current) {
      firstLastPage.current = { firstPage: 1, lastPage: 1 }
      currPageRef.current = 1
    }
    if (isLocationSeacrh.current) {
      const tagsTop = window.pageYOffset + document.getElementById("tags")!.getBoundingClientRect().top  as number
      window.scrollTo(0,tagsTop)
    }
    isFirstItems.current = true
    fetchAllProducts()
    // eslint-disable-next-line
  }, [tags, sortProperty, orderProperty, itemsPerPage])
  useEffect(() => {
    if (currPage === 1 && hasPrev) {
      setHasPrev(false)
    }
    fetchAllProducts()
    // eslint-disable-next-line
  }, [currPage])
  useEffect(() => {
    if (window.location.search) {
      let params: { [key: string]: string } = {}
      for (const [key, value] of searchParams) {
        params[key] = value
      }
      dispatch(setFilters({ ...params }))
      let searchP = Number(params["p"])
      if (searchP > 1) {
        firstLastPage.current = { firstPage: searchP, lastPage: searchP }
        setHasPrev(true)
      }
      isLocationSeacrh.current = true
    }
    isMounted.current = true
    // eslint-disable-next-line
  }, [])
  useEffect(()=> {
    let pagesCount = totalCount ? Math.ceil(totalCount / itemsPerPage) : 1
    if (firstLastPage.current.lastPage === pagesCount) {
      setHasMore(false)
    } else {
      setHasMore(true)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps 
  }, [ firstLastPage.current.lastPage, totalCount])
  return (
    <div className="page__tags tags-page" id="tags">
      <div className="tags-page__container container">
        <div className="tags-page__inner">
          <div className="tags-page__info info-tags">
            Характеристики ноутбука ни о чем вам не говорят? Воспользуйтесь нашим поиском по тегам: просто укажите для чего вам нужен ноутбук, какие функции у него должны быть, какие программы должен поддерживать. Можно выбрать несколько тегов. Выбирайте в  первую очередь важные для вас теги, чтобы второстепенные теги  не исключили интересные для вас варианты
          </div>
          <Tags />
          <div className="tags-page__catalog catalog-tags">
            <Sort sortProperty={sortProperty} orderProperty={orderProperty} setSort={setSort} />
            {hasPrev && <div className="catalog-tags__btn">{prevMoreLoading.current.prev ? <Loading /> : <button className="btn" onClick={() => loadPrevOnClick()}>Загрузить предыдущие...</button>}</div>}
            <div className="catalog-tags__items">
              {fetchStatus === "error" ?
                <div>Произошла ошибка :/</div> : 
                
                (isLoading && isFirstItems.current) ?
                  [...new Array(12)].map((item, i) => <Skeleton key={i} />) :
                  products.length ?
                  products.map((item, idx) => {
                      if (idx === currPage * itemsPerPage - 1) {
                        return <ProductBlock key={item.id} {...item} />
                      }
                      return <ProductBlock key={item.id} {...item} />
                    }
                    ) : <div>Нет соответствующих продуктов</div>}
            </div>
            {hasMore && <div className="catalog-tags__btn">{prevMoreLoading.current.more ? <Loading /> : <button className="btn" onClick={() => loadMoreOnClick()}>Загрузить еще...</button>}</div>}
          </div>
        </div>
      </div>
    </div>
  )
})
export default TagsPage