import { NavLink } from "react-router-dom"
import MainSwiper from "../MainSwiper"

const RecCatalog:React.FC = () => {
  return (
    <section className="page__recommendation rec-catalog" id="recommended">
    <div className="rec-catalog__container container">
      <div className="rec-catalog__inner">
        <h2 className="rec-catalog__title">Лучшие предложения</h2>
        <MainSwiper />
        <div className="rec-catalog__footer">
          <NavLink to={"/nbhunter/catalog"} className="btn" >Полный каталог</NavLink>
        </div>
      </div>
    </div>
  </section>
  )
}
export default RecCatalog