import { useLayoutEffect, useRef} from 'react'
import {gsap, Power2} from 'gsap'

const Preloader: React.FC = () => {
  const preloaderRef = useRef<HTMLDivElement>(null)
  useLayoutEffect(() => {
    let paddingValue = window.innerWidth - document.documentElement.clientWidth + 'px'
    document.body.style.cssText += `padding-right: ${paddingValue}`
    document.body.classList.add("no-scroll") 
    const preload = document.querySelector(".preloader") as HTMLElement
    const preloadBar = document.querySelector(".preloader__bar") as HTMLElement
    const preloadProgress = document.querySelector(".preloader__progress") as HTMLElement
    const preloadPercent = document.querySelector(".preloader__percent") as HTMLElement
    const preloadText = document.querySelector(".preloader__text-fill") as HTMLElement
    const percent = {value: 0}
    let ctx:gsap.Context
    ctx = gsap.context(() => {
      gsap.to(percent, {
          value: 100,
          duration: 1,           
          onUpdate: () => {
              preloadProgress.style.transform = "translateX("+ (percent.value - 100) + "%)"
              preloadPercent.textContent = percent.value.toFixed(0)
              preloadText.style.clipPath = "inset(" + (100 - percent.value) + "% 0% 0% 0%)"
        
          },
          onComplete: () => {
              preloadProgress.style.backgroundColor= "#126a88"
          },
          ease: Power2.easeInOut//CustomEase.create("custom", "M0,0 C0,0 0.461,0.009 0.534,0.06 0.622,0.122 0.623,0.082 0.756,0.25 0.88,0.406 0.908,0.465 0.928,0.518 0.978,0.652 1,1 1,1 ")
      })
      gsap.to(percent, {
          value: 0,
          duration: 0.5,
          delay: 1.1,
          onUpdate: () => {               
              preloadBar.style.height = ""+ (100 - percent.value) +"%"
              preloadText.style.clipPath = "inset(" + (100 - percent.value) + "% 0% 0% 0%)"            
          },
          ease: "power2.in"
      })
      gsap.to(percent, {
          value: 100,
          duration: 0.8,
          delay: 1.7,
          onUpdate: () => {             
              preload.style.cssText += "clip-path: inset(" + (percent.value) + "% 0% 0% 0%)"
              
          },
          onComplete: () => {
            preload.style.display = "none"
            document.body.style.paddingRight = "0px"
            document.body.classList.remove("no-scroll") 
          },
          ease: "power2.in"
      }) 
    }, preloaderRef); 
    return () => {if (ctx) ctx.revert()}; // cleanup
},[])
  return (
    <div className="preloader" ref={preloaderRef}>
      <div className="preloader__bar">
        <div className="preloader__progress"></div>
      </div>  
        <h1 className="preloader__inner">
            <span className="preloader__percent">0</span>
            <span className="preloader__text-stroke">NbHunter</span>
            <span className="preloader__text-fill">NbHunter</span>
        </h1>
    </div>
  )
}
export default Preloader