
import { NavLink } from "react-router-dom"
import React from "react"
import { ProductBlockType } from "../../common/types"

const ProductBlock = React.forwardRef<HTMLDivElement | null, ProductBlockType>(({id, imageURL, title, price, cpu, gpu, ram, hdd, ssd},ref) => {

  return (
    <div ref={ref} className="item-catalog">
      <NavLink to={"/nbhunter/product/" + id} className="item-catalog__preview">
      <div className="item-catalog__image">
        <img src={imageURL} alt="laptop"/>
      </div>
      </NavLink>
    <div className="item-catalog__body">
      <NavLink to={"/nbhunter/product/" + id} className="item-catalog__title">{title}</NavLink>
      <div className="item-catalog__details">
        <div className="item-catalog__detail">
          {cpu && cpu[0]} | {ram && ram[0]} ОЗУ 
        </div>
        <div className="item-catalog__detail">
          {hdd ? hdd[0] + " " + (ssd&&ssd[0]) : ssd? ssd[0]:""} | {gpu && gpu[0]}
        </div>
      </div>
      <div className="item-catalog__price rub">от {String(price).replace(/\B(?=(\d{3})+(?!\d))/g, " ").trim()}</div>
      <NavLink to={"/nbhunter/product/" + id} className="btn item-catalog__btn">Полные характеристики</NavLink>
    </div>
  </div>
  )
})
export default ProductBlock